.header_newsArea {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  height: 42px;
  background-color: #f3f3f3;
  position: relative;
}

.header_newsArea .header_breaking_news {
  position: absolute;
  top: 0;
  left: 0;
  color: #ffffff;
  background-color: #e75757;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  padding: 12px 28px;
  float: left;
}

.header_newsArea .header_breaking_news::after {
  content: '';
  position: absolute;
  left: 160px;
  top: 12px;
  border: 10px solid transparent;
  border-left: 10px solid #e75757;
  z-index: 10;
}

.header_newsArea .outer_breaking_news {
  width: 70%;
  margin-left: 162px;
  padding: 12px 0 0 12px;
  overflow: hidden;
}

@keyframes animMarqueeRtl {
  0% {
    transform: translateX(100%);
  }

  100% {
    transform: translateX(-100%);
  }
}

.header_newsArea .outer_breaking_news .stage_breaking_news {
  display: flex;
  align-content: center;
  width: 100%;
  white-space: nowrap;
  animation: animMarqueeRtl 45s linear infinite;
}

.header_newsArea .outer_breaking_news .stage_breaking_news .breaking_news {
  color: #2d2d2d;
  font-family: "Roboto", sans-serif;
  font-size: 12px;
  font-weight: 700;
  line-height: 18px;
  width: auto;
}

.header_newsArea .outer_breaking_news .stage_breaking_news .star {
  color: #aeaeae;
  width: 22px;
  padding: 2px 4px 0 4px;
}

.header_newsArea .header_social_icons {
  position: absolute;
  top: 0;
  right: 50px;
}

.header_newsArea .header_social_icons .search_icon,
.header_newsArea .header_social_icons .ball_icon,
.header_newsArea .header_social_icons .twitter_icon,
.header_newsArea .header_social_icons .facebook_icon {
  float: right;
  width: 42px;
  height: 42px;
  background-color: #e9e9e9;
  color: #9e9e9e;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  border-left: 1px solid #9e9e9e;
}

.header_newsArea .header_social_icons .search_icon:hover,
.header_newsArea .header_social_icons .ball_icon:hover,
.header_newsArea .header_social_icons .twitter_icon:hover,
.header_newsArea .header_social_icons .facebook_icon:hover {
  cursor: pointer;
}

